// Warning - code could be really messy , Using react js for the first time
import React from "react";
import ReactDOM from "react-dom";
import { withGesture } from "react-with-gesture";
import { Spring, animated } from "react-spring";
import "./styles.css";
import cardImage from "../images/GREassets_Card.png";
import meaningsFile from "../data/MeaningsFile.txt";
import "bootstrap/dist/css/bootstrap.min.css";
//import ModalDialog from "./modal";
//import MiniCard from "./miniCard";
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";
import { Breadcrumb, BreadcrumbItem } from "reactstrap";

const { convertCSVToArray } = require("convert-csv-to-array");
const converter = require("convert-csv-to-array");

var bgColors = {
  Default: "#81b71a",
  Blue: "#00B1E1",
  Cyan: "#37BC9B",
  Green: "#8CC152",
  Red: "#E9573F",
  Yellow: "#F6BB42"
};

var wrongWords = [];
var wrongWordsBreadCrumbs = [];
//document.body.style = "background: " + bgColors.Green;

//@withGesture - react doesn't support decorators as of now bitch!
const SliderComponent = withGesture(
  class Slider extends React.Component {
    constructor() {
      super();
      this.state = {
        word: "",
        meaning: "",
        score: 0,
        isNotMoving: true,
        leftWallOpacity: 0,
        rightWallOpacity: 0,
        meaningData: null,
        wrongAnswers: null
      };
      this.getMeanings();
    }

    divisionStyles = {
      position: "absolute",
      top: "25%",
      left: window.innerWidth / 3.2
    };

    imageStyles = {
      height: 321,
      width: 522,
      borderRadius: 38,
      justifyContent: "center",
      alignItems: "center"
    };

    ModalheaderStyles = {
      fontSize: 15,
      fontWeight: "bold"
    };

    headerStyles = {
      position: "absolute",
      top: "15%",
      left: "5%",
      fontSize: 38,
      fontWeight: "bold",
      flex: 1,
      flexGrow: 1,
      flexWrap: "wrap",
      flexDirection: "column"
    };

    descriptionTextStyles = {
      position: "absolute",
      top: "50%",
      left: "5%",
      right: "5%",
      color: "#4c4c4c",
      fontSize: 20,
      fontWeight: "bold",
      flex: 1,
      flexGrow: 1,
      flexWrap: "wrap",
      flexDirection: "column",
      backgroundImage: { cardImage }
    };

    breadCrumbheaderStyles = {
      fontSize: 16,
      fontWeight: "bold",
      color: "#000"
    };

    breadCrumbdescriptionStyle = {
      fontSize: 14,
      color: "#4c4c4c",
      fontWeight: "bold"
    };

    scoreHeaderTextStyles = {
      position: "absolute",
      top: "15%",
      left: "45%",
      fontSize: 38,
      fontWeight: "bold",
      flex: 1,
      flexGrow: 1,
      flexWrap: "wrap",
      flexDirection: "column",
      color: "#4c4c4c"
    };

    answerTypeHeaderTextStyles = {
      position: "absolute",
      top: "8%",
      left: "68%",
      fontSize: 24,
      fontWeight: "bold",
      flex: 1,
      flexGrow: 1,
      flexWrap: "wrap",
      flexDirection: "column",
      color: "#000",
      opacity: 1
    };

    getMeanings = () => {
      fetch("MeaningsFile.txt")
        .then(r => r.text())
        .then(text => {
          const arrayofArraysWithoutHeader = convertCSVToArray(text, {
            header: false,
            type: "array",
            separator: "," // use the separator you use in your csv (e.g. '\t', ',', ';' ...)
          });
          this.setState({ meaningData: arrayofArraysWithoutHeader });
          this.randomizeWordNew();
        });
    };

    resetEverything = () => {
      wrongWords = [];
      wrongWordsBreadCrumbs = [];
      this.setState({ score: 0 });
    };

    randomizeWordNew = () => {
      var wordIndex = this.getRandomInt(0, this.state.meaningData.length - 1);
      // do a little coin flip to decide wether to put the correct meaning or a different one
      var coinFlip = this.getRandomInt(0, 2);
      //console.log("coin flip :" + coinFlip);
      if (coinFlip == 0) {
        // put a random meaning
        this.setState({
          word: this.state.meaningData[wordIndex][0],
          meaning: this.state.meaningData[
            this.getRandomInt(0, this.state.meaningData.length - 1)
          ][1],
          direction: "none",
          isTrueMeaning: false
        });
      } else {
        // put the accurate meaning , this way , getting an accurate combination is more possible
        this.setState({
          word: this.state.meaningData[wordIndex][0],
          meaning: this.state.meaningData[wordIndex][1],
          direction: "none",
          isTrueMeaning: true
        });
      }
    };

    getRandomInt(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min)) + min; //The maximum is exclusive and the minimum is inclusive
    }

    checkMeaning = () => {
      return this.state.isTrueMeaning;
    };

    OnCardSwipe = (direction, x) => {
      if (
        this.state.isNotMoving === true ||
        direction != this.state.direction
      ) {
        if (direction === "Left") {
          this.setState({
            isNotMoving: false,
            rightWallOpacity: 0,
            leftWallOpacity: 0.5,
            direction: "Left"
          });
          console.log("Left X: " + x);
        } else if (direction === "Right") {
          this.setState({
            isNotMoving: false,
            lightWallOpacity: 0,
            rightWallOpacity: 0.5,
            direction: "Right"
          });
          console.log("Left X: " + x);
        }
      }
    };

    OnCardSwipeEnd = direction => {
      if (direction === "Left") {
        if (this.checkMeaning() === false) {
          this.setState({ score: this.state.score + 1 });
          //this.randomizeWord();
        } else {
          this.setState({ score: this.state.score - 1 });
          wrongWords.push([this.state.word, this.state.meaning]);
          console.log("appended to the list: " + this.wrongWords);
          //this.randomizeWord();
        }
      } else if (direction == "Right") {
        if (this.checkMeaning() === true) {
          this.setState({ score: this.state.score + 1 });
          //this.randomizeWord();
        } else {
          this.setState({ score: this.state.score - 1 });
          wrongWords.push([this.state.word, this.state.meaning]);
          console.log("appended to the list" + this.wrongWords);
          //this.randomizeWord();
        }
      }
    };

    render() {
      wrongWordsBreadCrumbs = wrongWords.map(d => (
        <li id="list_1">
          <div>
            <Breadcrumb>
              <BreadcrumbItem>
                <a style={this.breadCrumbheaderStyles}>{d[0]}</a>
              </BreadcrumbItem>
              <BreadcrumbItem>
                <a style={this.breadCrumbdescriptionStyle}>{d[1]}</a>
              </BreadcrumbItem>
            </Breadcrumb>
          </div>
        </li>
      ));
      const { xDelta, down, children } = this.props;
      return (
        <div>
          <div>
            <p style={this.scoreHeaderTextStyles}>score: {this.state.score}</p>
          </div>
          <gesture>
            <Spring
              native
              to={{ x: down ? xDelta : 0 }}
              immediate={name => down && name === "x"}
            >
              {({ x }) => (
                <div
                  className="item"
                  style={{
                    backgroundColor: xDelta < 0 ? "#ff7979" : "#badc58"
                  }}
                >
                  <animated.div
                    className="bubble"
                    style={{
                      transform: x
                        .interpolate({
                          map: Math.abs,
                          range: [50, 300],
                          output: [0.5, 1],
                          extrapolate: "clamp"
                        })
                        .interpolate(x => `scale(${x})`),
                      justifySelf: xDelta < 0 ? "end" : "start"
                    }}
                  />
                  <div
                    className="RightWall"
                    style={{
                      opacity: this.state.rightWallOpacity
                    }}
                  />
                  <div
                    className="LeftWall"
                    style={{ opacity: this.state.leftWallOpacity }}
                  />
                  <div>
                    <Button
                      color="secondary"
                      style={{ position: "fixed", top: "85%", left: "60%" }}
                      onClick={this.resetEverything}
                    >
                      <h style={this.ModalheaderStyles}>Reset</h>
                    </Button>
                  </div>
                  <animated.div
                    className="fg"
                    style={{
                      transform: x.interpolate(x => `translate3d(${x}px,0,0)`)
                    }}
                  >
                    <div>
                      <p style={this.headerStyles}>{this.state.word} </p>
                      <p style={this.descriptionTextStyles}>
                        {this.state.meaning}
                      </p>
                    </div>
                    {down && Math.abs(xDelta) > 250
                      ? xDelta < 0
                        ? (this.OnCardSwipe("Left", xDelta),
                          (
                            <code
                              style={this.answerTypeHeaderTextStyles}
                              className="badge badge-warning m-2"
                            >
                              FALSE
                            </code>
                          )) // swipe left keyword - cancel
                        : (this.OnCardSwipe("Right", xDelta),
                          (
                            <code
                              style={this.answerTypeHeaderTextStyles}
                              className="badge badge-warning m-2"
                            >
                              TRUE
                            </code>
                          )) // swipe right keyword - accept
                      : (children,
                        this.state.direction === "none"
                          ? ""
                          : this.setState({
                              direction: "none",
                              rightWallOpacity: 0,
                              leftWallOpacity: 0
                            }))}
                    {down && Math.abs(xDelta) > 250
                      ? ""
                      : down === false
                        ? this.state.isNotMoving === false
                          ? this.state.direction === "none"
                            ? ""
                            : (this.OnCardSwipeEnd(this.state.direction),
                              this.setState({
                                isNotMoving: true,
                                leftWallOpacity: 0,
                                rightWallOpacity: 0
                              }),
                              this.randomizeWordNew())
                          : ""
                        : ""}
                  </animated.div>
                </div>
              )}
            </Spring>
          </gesture>
          <div>
            <ModalDialog />
          </div>
        </div>
      );
    }
  }
);

export default SliderComponent;

class ModalDialog extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false
    };

    this.toggle = this.toggle.bind(this);
  }

  headerStyles = {
    fontSize: 15,
    fontWeight: "bold"
  };

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }

  render() {
    return (
      <div>
        <Button
          color="danger"
          style={{ position: "fixed", top: "85%" }}
          onClick={this.toggle}
        >
          <h style={this.headerStyles}>Errors</h>
        </Button>
        <Modal
          isOpen={this.state.modal}
          toggle={this.toggle}
          className={this.props.className}
        >
          <ModalHeader toggle={this.toggle}>What went wrong?</ModalHeader>
          <ModalBody>
            {" "}
            <ul style={{ listStyleType: "none" }}>{wrongWordsBreadCrumbs}</ul>
          </ModalBody>
          <ModalFooter>
            <Button color="warning" onClick={this.toggle}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}



